<template>
  <ul class="lxy-steps">
     <li  :class="item.className" v-for="(item,index) in infoList" :key="index">
        <slot :item="item">
            <div class="turning-steps-body">
              <p> {{item.title}}</p> 
              <p> {{item.description}}</p>   
           </div> 
        </slot>
     </li>
  </ul>
</template>

<script>
import classnames from "classnames"
export default {
  name:"StepsBar",
  computed:{
    infoList(){
      return this.list.map(item=>{
       const _item = {...item}
        const  className = classnames({
         active: +(item.status) === (+this.value)
        })
        _item.className = className
        return _item
      })
    }
  },
  props: {
    list:{
      type:Array,
      default:()=> [{
            status: "1",
            title: "1",
            description: "202177",
          },
          {
            status: "2",
            title: "2",
            description: "20211119",
          },
          {
            status: "3",
            title: "3",
            description: "20211231",
      }]
    },
    value:{
      type:String,
    }
  },
};
</script>

